/**=====================
     62. Responsive CSS Start
==========================**/

@media screen and (max-width: 1660px) {
    .blog-list {
        .blog-details {
            p {
                width: 100%;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }
    }
    .photos {
        ul {
            li {
                width: 22%;
            }
        }
    }
    .vertical-menu-main {
        .mega-menu {
            width: 1200px !important;
            max-width: 1200px !important;
            left: 0px !important;
        }
    }
    .user-status {
        table {
            thead {
                tr {
                    th {
                        &:nth-child(3) {
                            min-width: 124px;
                        }
                    }
                }
            }
            tbody {
                tr {
                    td {
                        &:first-child {
                            min-width: 278px;
                        }
                    }
                }
            }
        }
    }
    .employee-status {
        table {
            tbody {
                tr {
                    td {
                        &:first-child {
                            min-width: 300px;
                        }
                    }
                }
            }
        }
    }
    .bar-chart {
        canvas {
            width: 100% !important;
        }
    }
    .main-header-right {
        .main-header-right {
            .left-menu-header {
                .orm-control-plaintext {
                    .form-inline {
                        width: auto !important;
                    }
                }
            }
        }
    }
    .chat-box {
        .chat-history {
            .call-icons {
                margin-bottom: 15px;
                ul {
                    li {
                        width: 45px;
                        height: 45px;
                        a {
                            font-size: 18px;
                        }
                    }
                }
            }
            .total-time {
                h2 {
                    font-size: 40px;
                    color: $dark-gray;
                    margin-bottom: 10px;
                }
            }
            .call-content {
                .btn {
                    width: auto;
                    margin: 0 auto;
                }
            }
        }
        .chat-right-aside {
            .chat {
                .chat-header {
                    .chat-menu-icons {
                        li {
                            &:nth-child(n+2) {
                                margin-left: 10px;
                            }
                            a {
                                i {
                                    font-size: 18px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .chat-left-aside {
        .people-list {
            height: 520px;
        }
    }
    .chat-box {
        .chat-right-aside {
            .chat {
                .chat-msg-box {
                    height: 440px;
                }
            }
        }
        .people-list {
            ul {
                max-height: 450px;
                overflow: auto;
            }
        }
    }
    .call-chat-body {
        .chat-box {
            .people-list {
                ul {
                    max-height: 500px;
                    overflow: auto;
                }
            }
            overflow: hidden;
            height: 625px;
        }
    }
    .caller-img {
        img {
            opacity: 0.7;
        }
    }
    .chat-box {
        .chat-history {
            .receiver-img {
                margin-top: 25px;
            }
            .call-content {
                min-height: 545px;
            }
        }
    }
    // search //
    .search-page {
        .search-vid-block {
            >div {
                margin-top: 30px;
            }
        }
    }
    // user profile //
    .user-profile {
        .photos {
            ul {
                li {
                    width: 21%;
                }
            }
        }
    }
    // email wrap
    .email-wrap {
        .email-right-aside {
            .email-profile {
                .inbox {
                    p {
                        width: calc(100% - 300px);
                        max-width: 500px;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 1550px) {
    .bg-gallery {
